<template>
  <span class="card__status" :class="getColor()">{{ statusName[this.name] }}</span>
</template>

<script>
import { statusName } from "@/const";
export default {
  props: {
    name: String,
  },
  data() {
    return {
      color: null,
      statusName,
    };
  },
  methods: {
    getColor() {
      switch (statusName[this.name]) {
        case "":
        case " ":
        case "":
          return "brown";
        case "":
        case "":
        case "":
          return "blue";
        case "":
          return "red";
      }
    },
  },
};
</script>

<style lang="scss">
@import "./index.scss";
</style>